import VueStore from '@/vue-store';

import App from '@/App.vue';
import router from '@/router';
import store from '@/store';

VueStore.config.productionTip = false;

new VueStore({
  router,
  store,
  beforeCreate() {
    store.commit('initialiseStore');
  },
  render: (h) => h(App),
}).$mount('#app');
