import Vue from 'vue';
import Vuex, { Store } from 'vuex';

Vue.use(Vuex);

export type Language = 'ru' | 'fi';

export interface IState {
  language: Language;
}

const store: Store<IState> = new Vuex.Store({
  state(): IState {
    return {
      language: 'ru',
    };
  },

  mutations: {
    initialiseStore(state) {
      const savedStoreStr = localStorage.getItem('tamflow');
      if (savedStoreStr) {
        const savedStore = Object.assign(state, JSON.parse(savedStoreStr ?? ''));
        this.replaceState(savedStore);
      }
    },
    setLanguage(state: IState, lang: Language) {
      state.language = lang;
    },
  },
});

store.subscribe((mutation, state) => {
  localStorage.setItem('tamflow', JSON.stringify(state));
});

export default store;
