
import Vue, { VueConstructor } from 'vue';
import { Store } from 'vuex';
import { IState } from '@/store';

abstract class VueStoreClass<S extends Store<any>> extends Vue {
    public $store!: S;
}

const VueStore = Vue as VueConstructor<VueStoreClass<Store<IState>>>;

export default VueStore;
