






import { Component } from 'vue-property-decorator';

import VueStore from '@/vue-store';
import { Language } from '@/store';

@Component({})
export default class About extends VueStore {
  get header() {
    return this.$store.state.language === 'ru' ?
      'О TamFlist' :
      'TamFlistista';
  }

  get content() {
    return this.$store.state.language === 'ru' ?
      'Однажды я подумал, что мысль "О, какой красивый цветок! Интересно, как же он называется?" слишком часто приходила мне в голову. Да, с десяток названий цветков я знал (например, ромашка, одуванчик, мать-и-мачеха, василёк, лютик, купальница). Но вот в один солнечный день я нарвал незнакомых мне цветов, принёс домой, сел за компьютер, и начал разбираться... Поначалу я делал только закладки веб-страниц на которых было описание тех цветов что лежали предо мной. Но когда их набралось свыше 50, стало понятно, что нужен более наглядный способ чтобы выучить их всех. А заодно разобраться как Vue в 2018 году умеет дружить с TypeScript. Так появился TamFlist, мой каталог цветущих трав в Тампере и его окрестностях.' :
      'Eräänä päivänä ajattelin, että ajatus "Voi, mikä kaunis kukka! Mietin, miksi sitä kutsutaan?" tuli mieleeni liian usein. Kyllä, tiesin kymmenkunta kukkanimeä (esimerkiksi kamomilla, voikukka, varsajalka, ruiskukka, leinikki, uimapuku). Mutta sitten eräänä aurinkoisena päivänä poimin tuntemattomia kukkia, toin ne kotiin, istuin tietokoneen ääreen ja aloin pohtimaan sitä... Aluksi laitoin kirjanmerkkeihin vain verkkosivut, joilla oli kuvaus noista kukista. Mutta kun niitä oli yli 50, kävi selväksi, että tarvitsen visuaalisemman tavan oppia ne kaikki. Ja samalla selvitä, kuinka Vue vuonna 2018 voi olla ystävä TypeScriptin kanssa. Näin syntyi TamFlist, kukkien yrttihakemistoni Tampereella ja sen ympäristössä.';
  }
}
